import styled, { css } from 'styled-components';
import { radius_prm, space_prm, convertToScss, borderProperty } from '../cssPropertes';

export const StyleComp = ({ attributes, children }) => {
  return (
    < StyledDiv attributes={attributes} >
      {children}
    </StyledDiv >
  );
}

const StyledDiv = styled.div`
  ${({ attributes }) => {

    const {
      focusColor,
      bgColor,
      font_style_input,
      bgColor_input,
      bgGradient_input,
      textColor_input,
      radius_input,
      border_input,
      default_pos,
      mobile_pos,
      font_style_label,
      bgColor_label,
      bgGradient_label,
      textColor_label,
      radius_label,
      border_label,
      padding_label,
      labelSpace,
      labelWidth,
      areaHeight,
      shadow_result,
      is_shadow,
      className,
    } = attributes;

    //
    const bgInputColor = bgColor_input || bgGradient_input;
    const bgLabelColor = bgColor_label || bgGradient_label;
    //
    const fontStyle_input = font_style_input.isItalic ? "italic" : "normal";
    const fontStyle_label = font_style_label.isItalic ? "italic" : "normal";
    //
    const input_radius_prm = radius_prm(radius_input);
    const label_radius_prm = radius_prm(radius_label);
    //
    const default_form_margin_prm = space_prm(default_pos.margin_input);
    const default_form_padding_prm = space_prm(default_pos.padding_input);
    const mobile_form_margin_prm = space_prm(mobile_pos.margin_input);
    const mobile_form_padding_prm = space_prm(mobile_pos.padding_input);
    const label_padding_prm = space_prm(padding_label);
    //
    const box_shadow_style = is_shadow && shadow_result ? convertToScss(shadow_result) : ''


    // 
    const commonStyle = css`
      
      padding: ${default_form_padding_prm};
      margin: ${default_form_margin_prm};
      background: ${bgColor};
      position: relative;
      @media (max-width: 767px) {
        margin: ${mobile_form_margin_prm};
        padding: ${mobile_form_padding_prm};
      }
      input, textarea{
        flex-grow: 1;
        background: ${bgInputColor};
        border-radius: ${input_radius_prm};
        color: ${textColor_input};
        font-size: ${font_style_input.fontSize};
        font-family: ${font_style_input.fontFamily};
        font-weight: ${font_style_input.fontWeight};
        font-style: ${fontStyle_input};
      }
      textarea{
        min-height: 60px;
        box-sizing: border-box;
        padding: 6px 10px;
      }
      label{
        display: inline-block;
        vertical-align: top;
        white-space: nowrap;
        background: ${bgLabelColor};
        border-radius: ${label_radius_prm};
        color: ${textColor_label};
        font-size: ${font_style_label.fontSize};
        font-family: ${font_style_label.fontFamily};
        font-weight: ${font_style_label.fontWeight};
        font-style: ${fontStyle_label};
        padding: ${label_padding_prm};
        ${borderProperty(border_label)};
        ${box_shadow_style};
        span{
          color:var(--wp--preset--color--accent-1);
        }
      }
      
    `;

    // className
    let specificStyle = null;
    switch (className) {
      case 'is-style-line':
        specificStyle = css`
          display: flex;
          height: auto;
          ${box_shadow_style};
          input, textarea{
            background-color: transparent;
            outline: none;
            padding: 0 8px;
            border-style: solid;
            border-color: ${bgColor_input};
            border-width: 0px 0px 2px 0px;
            box-shadow: none;
            transition: border-color 0.45s ease 0s;
            transform: translateY(1em);
            
            &:focus {
              box-shadow: none;
              border-color: ${focusColor};
              ~label
              {
                opacity: 1;
                z-index: 1;
                top: -0.3em;
                font-size: 0.8em;
              }
              ~label{
                color:${focusColor}
              }
            }
            &:not(.empty)~label {
              opacity: 1;
              z-index: 1;
              top: -0.3em;
              font-size: 0.8em;
            }
          }
          textarea{
            padding: 8px 10px;
          }
          
          label{
            position: absolute;
            width: fit-content;
            opacity: 0;
            left: calc(2em + 10px);
	          pointer-events: none;
            bottom:15px;
	          z-index: 1;
            transition: all 0.5s cubic-bezier(.68, -0.55, .27, 1.55) 0s;
            
          }
        `
        break;
      default:
        specificStyle = css`
            display: flex;
            flex-direction: row-reverse;
            @media (max-width: 767px) {
              flex-direction: column-reverse;
            }
            input, textarea{
              ${borderProperty(border_input)};
              ${box_shadow_style};
              transition: box-shadow 0.45s ease 0s;
              &:focus {
                outline: none;
                box-shadow: 0 0 5px ${focusColor};
              }
            }
            label{
              width:${labelWidth};
              margin-right: ${labelSpace};
              @media (max-width: 767px) {
                margin-bottom: 15px;
              }
            }
          `;
    }


    // 
    return css`
      ${commonStyle}
      ${specificStyle}
    `;
  }
  }
    `;
